@import url(https://fonts.googleapis.com/css?family=Cairo);
html,
body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #282c34;
	font-family: 'Cairo', sans-serif;
	font-weight: 400;
	color: #FAFAFA;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.header h1 {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
	margin: 20px auto 20px auto;
	font-family: 'Cairo', cursive;
}

.project {
  display: block;
  padding: 10px;
  margin: 0 0 10px 0;
  color: black;
  background: #eee;
  font-family: 'Cairo', cursive;
  font-weight: bold
}

.project a:link {
  color: black
}

.project a:visited {
  color: black
}

.project span.success {
  color: green;
}

.project span.pending {
  color: orange;
}

.project span.failure {
  color: red;
}

.project button.remove{
  color: red;
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-left: 10px;
  font-weight: bold;
  font-size: .9rem;
}
.search{
    margin-bottom: 10px;
    height: 150px;
    overflow: scroll;
}
.search .input{
    padding: 5px;
    font-size: 1rem;
    width: 20%;
}
.search .button{
    padding: 5px;
    font-size: 1rem;
    margin-left : 10px;
}
.error-message{
    background: tomato;
    width: 30%;
    margin-left: 35%;
    margin-top: 1%;
    padding: 0px;
    color: #fff;
    border-radius: 10px;
    opacity: 1;
    -webkit-transition: opacity 0.5s ease;
    transition: opacity 0.5s ease;
}
.close {
    font-size: 14px;
    cursor: pointer;
    border-radius: 50%;
    margin-left:5%
}
.react-tabs {
    -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
    /* border-bottom: 1px solid #aaa; */
    margin: 0 0 10px;
    padding: 0;
}

.react-tabs__tab {
    display: inline-block;
    background-color: #1180F2;
    border: 1px solid transparent;
    color: black;
    border-bottom: none;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 6px 12px;
    cursor: pointer;
}

.react-tabs__tab--selected {
    background: #0D65CA;
    color: #fff;
    /* border-radius: 5px 5px 0 0; */
}

.react-tabs__tab--disabled {
    color: GrayText;
    cursor: default;
}

.react-tabs__tab-panel {
    display: none;
}

.react-tabs__tab-panel--selected {
    display: block;
}
table {
    font-family: arial, sans-serif;
    font-size: 0.8rem;
    font-weight: normal;
    border-collapse: collapse;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }
